*{
    margin:0px;
}

.container {
    overflow: auto;
    height: 100vh;
    width: 100vw;
    background-color: #121212;
    display: grid;
    justify-content: center;
    align-content: center;
}

table{
    width: 60vw;
    border-spacing: 0px;
}

tr{
    color:white;
    height: 40px;
}

.symbol{
    text-align: start;
}

td{
    
    border-bottom: 1px solid #212121;
    border-top: 1px solid #212121;
}

tr:hover{
    background-color: #212121;
    color: #1db954;
    border-right: 10px solid #212121;
}

.Right{
   text-align: end;
}

img{
    margin-left: 10px;
    margin-right: 10px;
}